
.displayingArea {
    display: flex;
    height: 75vh;
    .leftArea, .rightArea {
        width: 50%;
        height: 100%;
        display: flex;
        justify-content: center;
        margin-top: 8%; 
    }
    .leftArea {
        .ant-card-actions {
            border: none;
        }
    }
    .rightArea {
        .ant-tabs .ant-tabs-tab .anticon {
            margin-right: 0;
        }
        .ant-tabs-tab-active {
            div {
                background-color: #1677ff;
                color: #ffffff;
                border-radius: 0.5rem;
            }
        }
    }
}

.icon1, .icon2, .icon3, .icon4 {
    width: 120px;
    height: 60px;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.icon1:hover, .icon2:hover, .icon3:hover, .icon4:hover {
    color: #ffffff;
    // background-color: #ff7f00;
    background-color: #1677ff;
    cursor: pointer;
}